export function exportToCsv(filename: string, rows: Record<string, any>[]) {
  if (!rows || !rows.length) {
    return;
  }

  // Define field order for better CSV structure
  const fieldOrder = [
    'requestor',
    'customer',
    'projectName',
    'productName',
    'type',
    'targetBomCost',
    'articleCode',
    'ltoRegular',
    'monthlyVolume',
    'monthlyRevenue',
    'productStatus',
    'shelfLife',
    'packSize',
    'details'
  ];

  // Create headers with proper labels
  const headers = fieldOrder.map(field => {
    const label = field
      .replace(/([A-Z])/g, ' $1') // Add space before capital letters
      .replace(/^./, str => str.toUpperCase()) // Capitalize first letter
      .replace('Bom', 'BOM') // Fix specific acronyms
      .replace('Lto', 'LTO');
    return label;
  });

  // Create CSV content
  const csvContent = [
    headers.join(','),
    ...rows.map(row => 
      fieldOrder.map(field => {
        let cell = row[field] === null || row[field] === undefined ? '' : row[field];
        cell = cell instanceof Date ? cell.toLocaleString() : cell.toString().replace(/"/g, '""');
        return cell.search(/("|,|\n)/g) >= 0 ? `"${cell}"` : cell;
      }).join(',')
    )
  ].join('\n');

  // Create and download file
  const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
  const link = document.createElement('a');
  const url = URL.createObjectURL(blob);
  link.setAttribute('href', url);
  link.setAttribute('download', filename);
  link.style.display = 'none';
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
  URL.revokeObjectURL(url);
}
